<template>
  <div id="potato-clock">
    <div class="circle">
      <svg>
        <circle cx="50%" cy="50%" r="44%" stroke="#BA000D" stroke-width="5" />
        <circle cx="50%" cy="50%" r="40%" fill="#F44336" />
      </svg>
    </div>
    <h1 id="countdown-text" class="display-1">{{ timerText }}</h1>
    <h2 class="display-5">THE FIRST THING TO DO</h2>
    <button @click="startCountdown()">play</button>
  </div>
</template>
<style lang="scss">
#potato-clock {
  width: 45vw;
  height: 100vh;
  color: white;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .circle svg {
    height: 300px;
  }
}
</style>
<script>
module.exports = {
  data: function() {
    return {
      duration: 1*60,
      timerText: '01:00'
    };
  },
  methods: {
    startCountdown: function() {
      let vm = this;
      let timerInterval = setInterval(function() {
        minute = Math.floor(vm.duration / 60)
        second = vm.duration % 60

        minute = minute < 10 ? "0"+minute : minute
        second = second < 10 ? "0"+second : second

        if(--vm.duration < 0){
            vm.duration = 1*60
            clearInterval(timerInterval)
        }
        vm.timerText = minute + ':' + second
      }, 1000);
    }
  }
};
</script>
