<template>
  <div id="todo-list-content">
    <div class="input-group">
      <input
        v-model="newTodoText"
        type="text"
        class="form-control"
        placeholder="ADD NEW MISSION"
        aria-label="Recipient's username"
        aria-describedby="basic-addon2"
      />
      <div class="input-group-append">
        <button v-on:click="addTodo" class="btn btn-outline-dark add-button" type="button">
          <i class="material-icons orange600">add</i>
        </button>
      </div>
    </div>
    <div class="todos">
      <div class="content-title">
        <h4>TO-DO</h4>
        <a class="btn" data-toggle="collapse" href="#collapse-todos">
          <i class="material-icons white">arrow_drop_down</i>
        </a>
      </div>

      <ul class="collapse show" id="collapse-todos">
        <li v-for="todo in todos" v-bind:key="todo.id">
          <div class="content">
            <button @click="deleteTodo(todo)" class="btn clear-button">
              <i class="material-icons orange600">clear</i>
            </button>
            {{todo.text}}
          </div>
          <div>
            <i class="material-icons">play_circle_outline</i>
          </div>
        </li>
      </ul>

      <div class="content-title">
        <h4>DONE</h4>
        <a class="btn" data-toggle="collapse" href="#collapse-done">
          <i class="material-icons white">arrow_drop_down</i>
        </a>
      </div>
      <ul class="collapse show" id="collapse-done">
        <li v-for="todo in doneTodos" v-bind:key="todo.id">
          <div class="content">
            <i class="material-icons mr-3">check_circle_outline</i>
            <del>{{todo.text}}</del>
          </div>
          <div>
            <i class="material-icons">play_circle_outline</i>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
module.exports = {
  data: function() {
    return {
      newTodoText: "",
      todoList: [
        {
          id: 0,
          text: "買角鋼",
          done: false
        },
        {
          id: 1,
          text: "女友的生日蛋糕",
          done: true
        },
        {
          id: 2,
          text: "練字",
          done: true
        },
        {
          id: 3,
          text: "寫作業",
          done: false
        },
        {
          id: 4,
          text: "整理桌面",
          done: false
        }
      ]
    };
  },
  methods: {
    addTodo: function() {
      this.todoList.push({
        id: ++this.nextTodoId,
        text: this.newTodoText,
        done: false
      });
    },
    deleteTodo: function(todo) {
      this.todoList.splice(this.todoList.indexOf(todo), 1);
    }
  },
  computed: {
    nextTodoId: function() {
      return this.todoList.length;
    },

    doneTodos: function() {
      return this.todoList.filter(todo => todo.done);
    },
    todos: function() {
      return this.todoList.filter(todo => !todo.done);
    }
  }
};
</script>

<style lang="scss">
#todo-list-content {
  width: 100%;
  .add-button {
    padding: 0 12px;
    border-left: none;
  }
  .clear-button {
    padding: 0;
    height: 24px;
    margin-right: 16px;
    border: none;
  }
  input {
    color: #f44336;
    font-size: 24px;
    border-color: black;
    border-right: none;
    &::placeholder {
      color: #f44336;
    }
  }
}
.content-title {
  margin-top: 30px;
  padding: 5px 12px;
  display: flex;
  background-color: black;
  justify-content: space-between;
  align-items: center;
  h4 {
    color: white;
  }
}
ul {
  list-style: none;
  padding-left: 0px;
  li {
    padding: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    border-bottom: #707070 1px solid;
    .content {
      display: flex;
      align-items: center;
    }
  }
}
</style>
